const docureBannerStyle = `
  :host {
    display: flex;
    position: relative;
    width: 100%;
    height: 400px;
  }
  :host div.docure-banner-vanilla-content {
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    background: #dc493a;
    padding: 10px 30px;
  }
  :host div.docure-banner-vanilla-image {
    width: 30%;
  }
  div.docure-banner-vanilla-image::before {
    position: absolute;
    display: block;
    content: ' ';
    top: 0;
    bottom: 0;
    right: 0;
    left: 70%;
    background-image: url('data:image/svg+xml;base64,');
    background-size: auto 400px;
    background-repeat: no-repeat;
  }
`;

const docureBannerTitleStyle = `
  :host {
    height: 20%;
  }

  :host p {
    color: white;
    text-align: left;
    font-size: large;
    font-weight: bold;
    text-transform: uppercase;
  }
`;

const docureBannerContentStyle = `
  :host {
    height: 60%;
    width: 80%;
  }
  
  :host div {
    color: #fff;
  }
`;

const docureBannerActionsStyle = `
  :host {
    height: 20%;
  }
`;

class DocureBanner extends HTMLElement {
  constructor() {
    super();
    console.log('[DocureBanner] constructor');
  }

  connectedCallback() {
    console.log('[DocureBanner] connectedCallback - added in DOM');
    if (this.hasAttribute('imagesrc')) {
      const shadowDOM = this.attachShadow({ mode: 'closed' });

      const styleElement = document.createElement('style');
      styleElement.appendChild(document.createTextNode(docureBannerStyle));

      const contentDiv = document.createElement('div');
      contentDiv.className += 'docure-banner-vanilla-content';
      contentDiv.innerHTML = '<slot  />';

      const imageDiv = document.createElement('div');
      imageDiv.className += 'docure-banner-vanilla-image';
      imageDiv.style.backgroundImage = `url(${this.getAttribute('imagesrc')})`;
      imageDiv.style.backgroundSize = 'auto 400px';
      imageDiv.style.backgroundRepeat = 'no-repeat';

      shadowDOM.appendChild(styleElement);
      shadowDOM.appendChild(contentDiv);
      shadowDOM.appendChild(imageDiv);
    }
  }
}

class DocureBannerTitle extends HTMLElement {
  constructor() {
    super();
    console.log('[DocureBannerTitle] constructor');
  }

  connectedCallback() {
    console.log('[DocureBannerTitle] connectedCallback - added in DOM');
    if (this.hasAttribute('text')) {
      const shadowDOM = this.attachShadow({ mode: 'closed' });

      const styleElement = document.createElement('style');
      styleElement.appendChild(document.createTextNode(docureBannerTitleStyle));

      const pElement = document.createElement('p');
      pElement.innerHTML = this.getAttribute('text');

      shadowDOM.appendChild(styleElement);
      shadowDOM.appendChild(pElement);
    }
  }
}

class DocureBannerContent extends HTMLElement {
  constructor() {
    super();
    console.log('[DocureBannerContent] constructor');
  }

  connectedCallback() {
    console.log('[DocureBannerContent] connectedCallback - added in DOM');

    const shadowDOM = this.attachShadow({ mode: 'closed' });

    const styleElement = document.createElement('style');
    styleElement.appendChild(document.createTextNode(docureBannerContentStyle));

    const divElement = document.createElement('div');
    divElement.innerHTML = '<slot/>';

    shadowDOM.appendChild(styleElement);
    shadowDOM.appendChild(divElement);
  }
}

class DocureBannerActions extends HTMLElement {
  constructor() {
    super();
    console.log('[DocureBannerActions] constructor');
  }

  connectedCallback() {
    console.log('[DocureBannerActions] connectedCallback - added in DOM');

    const shadowDOM = this.attachShadow({ mode: 'closed' });

    const styleElement = document.createElement('style');
    styleElement.appendChild(document.createTextNode(docureBannerActionsStyle));

    shadowDOM.appendChild(styleElement);
    shadowDOM.appendChild(document.createElement('slot'));
  }
}

if (!customElements.get('docure-banner-vanilla-title'))
  customElements.define('docure-banner-vanilla-title', DocureBannerTitle);

if (!customElements.get('docure-banner-vanilla-content'))
  customElements.define('docure-banner-vanilla-content', DocureBannerContent);

if (!customElements.get('docure-banner-vanilla-actions'))
  customElements.define('docure-banner-vanilla-actions', DocureBannerActions);

if (!customElements.get('docure-banner-vanilla'))
  customElements.define('docure-banner-vanilla', DocureBanner);
